<template>
  <div class="btn_float">
    <a href="javascript:void(0)" v-for="(item,index) in btn_float" :key="index">
      <button :class="{'active': index == activeIndex}" @click="changeActive(index)">{{item}}</button>
    </a>
  </div>
</template>

<script>
export default {
  name: "btnfloat",
  props: {
    btn_float: Array
  },
  data() {
    return {
      activeIndex: 0
    };
  },
  methods: {
    changeActive(index) {
      this.activeIndex = index;
      this.$emit("getIndex", index);
    }
  }
};
</script>

<style lang='scss' scoped>
div.btn_float {
  padding: 12px;
  box-sizing: border-box;
  border-bottom: 1px dashed rgba(204, 204, 204, 1);
  a {
    text-decoration: none;
    margin-left: 12px;
    &:first-child {
      margin-left: 0;
    }
    button {
      border: 0;
      outline: none;
      font-size: 12px;
      font-weight: 400;
      color: rgba(87, 87, 87, 1);
      width: calc((100% - 36px) / 4);
      height: 24px;
      background: rgba(228, 228, 228, 1);

      border-radius: 12px;
      &.active {
        background: rgba(43, 142, 226, 1);
        color: rgba(255, 255, 255, 1);
      }
    }
  }
}
</style>